<template>
  <div id="user-profile">
    <div v-if="isSignedIn" class="form-container">
      Signed in as {{this.$cookie.get('fritter-auth')}}
      <!-- <FritterTitle/> -->
      <SignOut/>
      <div class="four wide column">
        <img class="ui tiny avatar image" src="https://www.gravatar.com/avatar/default?s=200&r=pg&d=mm">
      </div>
      <ChangeUserCredentials/>
    </div>
    <div v-else class="form-container">
          <template >
            <b-navbar-item tag="router-link" :to="{ path: '/account' }">
                    <a class="button is-light">
                        <strong>Go to sign in page</strong>
                    </a>

          </b-navbar-item>
        </template>
      

    </div>
    <div v-if='messages.length' class="success-message" style="text-align:center;">
      <div v-for='message in messages' v-bind:key='message.id'>{{ message }}</div>
    </div>
  </div>
</template>

<script>
import ChangeUserCredentials from "./ChangeUserCredentials.vue";
import FritterTitle from "../components/FritterTitle.vue";
import SignOut from "../components/SignOut.vue";

import { eventBus } from "../main";

export default {
  name: "UserProfile",

  components: {
    ChangeUserCredentials,
    FritterTitle, 
    SignOut,

  },

  data() {
    return {
      isSignedIn: false,
      messages: []
    };
  },

  created: function() {
    let authenticated = this.$cookie.get('fritter-auth');
    if (authenticated) {
      this.isSignedIn = true;
    }

    eventBus.$on("change-username-success", () => {
      this.$cookie.set('fritter-auth', '');
      this.isSignedIn = false;
      this.messages.push(`Your username was successfully changed! Please sign in again.`);
      this.clearMessages();

    });

    eventBus.$on("change-password-success", () => {
      this.$cookie.set('fritter-auth', '');
      this.isSignedIn = false;
      this.messages.push(`Your password was successfully changed! Please sign in again.`);
      this.clearMessages();

    });

    eventBus.$on("signout-success", () => {
      this.$cookie.set('fritter-auth', '');
      this.isSignedIn = false;
      // this.messages.push("You have been signed out!");
      // this.clearMessages();
    });
  },
  methods: {
    clearMessages: function() {
      setInterval(() => {
        this.messages = [];
      }, 5000);
    },

  }
};
</script>