<template>
  <div>
    
    <div v-if="isSignedIn" class="form-container">
      <CreateFreetForm/>
      <FreetList/>
      <!-- <MyFreetList/> -->
    </div>
    <div v-else class="form-container">
      <FreetList/>
    </div>  
  </div>
</template>

<script>

import CreateFreetForm from "../components/CreateFreetForm.vue";
import FreetList from "../components/FreetList.vue";
import MyFreetList from "../components/MyFreetList.vue";


// import { eventBus } from "../main";




export default {
  name: "main",
  components: {
    CreateFreetForm,
    FreetList,
    MyFreetList,
  },

  data() {
    return {
      isSignedIn: false,
      messages: []
    };
  },

  created: function() {
    let authenticated = this.$cookie.get('fritter-auth');
    if (authenticated) {
      this.isSignedIn = true;
    }
  },
};
</script>